    border:1px solid black;
    margin:10px;
    font-size: 2em;
 }

.skillbar span:hover{
    background: white;
    color:#2196F3;
}

a{
    text-decoration: none;
    outline:none;
}
.rightnav{
    position:fixed;
}

img{
 width:60%;
 height:60%;
 border-radius: 60%;
}

.pic{
    position:relative;
    left:10%;
    top:100px;
}
.contact{
    position:relative;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    left:16%;
    top:200px;
}
.contact img{
    padding-top:15px;
    height:100%;
    width:10%;
}

.contact div span{
    font-family:'Special Elite', cursive;
    font-size: 1.3rem;
    color:#2196F3;
    position:relative;
    top:-20px;
}

.contact img:hover{
    width:12%;
}

.pic_mobile img{
    position:relative;
    width:60%;
    height:60%;
    border-radius: 60%;
    left:20%;
    top:50px;
   }
.ham{
    padding:0;
    margin:0;
}

.ham button{
    position:fixed;
    top:10px;
    right:10px;
    height:40px;
    width:60px;
    background:none;
    z-index: 200;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    border:0;
}

.ham button div{
    padding:0;
    margin:0;
    height:3px;
    width:35px;
    background:#2196F3;
}

.greysheet{
    position:fixed;
    height:100vh;
    width:100vh;
    background:grey;
    opacity: 0.6;
    z-index:100;
    -webkit-transform:translateX(500%);
            transform:translateX(500%);
    -webkit-transition:-webkit-transform 0.4s ease-in;
    transition:-webkit-transform 0.4s ease-in;
    transition:transform 0.4s ease-in;
    transition:transform 0.4s ease-in, -webkit-transform 0.4s ease-in;
}

.greysheet .open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.Drawer{
    font-family:'Special Elite', cursive;
    font-size:1.3em;
    height:100vh;
    position:fixed;
    width:60%;
    z-index: 200;
    background:white;
    box-shadow: 10px 10px 100px 5px black;
    -webkit-transform: translateX(-500%);
            transform: translateX(-500%);
    -webkit-transition: -webkit-transform 0.4s ease-out;
    transition: -webkit-transform 0.4s ease-out;
    transition: transform 0.4s ease-out;
    transition: transform 0.4s ease-out, -webkit-transform 0.4s ease-out;
}

.space{
    margin-top:15vh;
    padding:0;
    height:20vh
}

.link{
    cursor:pointer
}

.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}




.mobile{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.space{
    height:50px;
    width:100%;
}
